.App {
  text-align: center;
  background: #fafafa;
  color: #444;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.icon {
  width: 150px;
  height: 150px;
  border-radius: 25px;
  box-sizing:border-box;
  -webkit-box-sizing:border-box;
  -moz-box-sizing:border-box;
  border: 1px solid rgba(0,0,0,0.1);
  margin-bottom: 10px;
}

h1 {
  font-size: 22px;
  padding-bottom: 0px;
  margin-bottom: 0px;
}

p {
  max-width: 320px;
}

.downloadBadges {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}

.downloadBadges img {
  padding: 10px;
}
